button.swal2-styled[type="button"] {
  border-radius: 0 !important;
  font-family: "Oswald" !important;
}

button.swal2-styled[type="button"].swal2-confirm {
  background-color: #D43425 !important;
}

button.swal2-styled[type="button"].swal2-cancel {
  background-color: #4A4A4A !important;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans";
}

#root, body, html {
  height: 100%;
}

/* Fix issue where sweetalert2 breaks layout */
body.swal2-height-auto.swal2-height-auto.swal2-height-auto {
  height: 100% !important;
}

* {
  box-sizing: border-box;
}

/* remove default user agent style */
a {
  text-decoration: none;
  color: inherit;
}
